import React, { Component } from 'react';
import { Button, Modal, Form } from 'semantic-ui-react';
import { FiX } from 'react-icons/fi';
import Validator from 'validator';

// Services
import axiosService from '../../services/axiosService';
import toastService from '../../services/toastService';

class RegisterModal extends Component {
    constructor(props) {
        super(props);

        this.state = {
            open: false,
            name: '',
            nameError: '',
            email: '',
            emailError: '',
            pass: '',
            passError: '',
            repass: '',
            repassError: '',
            btnDisable: false,
            btnLoading: false
        };
    }

    componentWillMount() {
        this.setState({
            open: this.props.modalState
        });
    }

    componentWillReceiveProps(nextProps) {
        this.setState({
            open: nextProps.modalState
        });
    }

    /**
     * This function closes the modal by setting open state to false
     */
    closeModal = () => {
        this.setState({
            open: false
        })
    }

    /**
     * This function updates input states
     */
    handleChange = () => (e) => {
        this.setState({ [e.target.name]: e.target.value });
        this.formErrors();
    }

    /**
     * form validation this will disable the signup button if the checks are not passed
     */
    formErrors = () => {
        if (this.state.name === '') this.setState({ nameError: 'Please enter your name' });
        else this.setState({ nameError: '' });

        if (!Validator.isEmail(this.state.email) || this.state.email === '') this.setState({ emailError: 'Please enter a valid email' });
        else this.setState({ emailError: '' });

        if (this.state.pass === '') this.setState({ passError: 'Please enter a valid passowrd' });
        else this.setState({ passError: '' });

        if (this.state.pass !== this.state.repass) this.setState({ repassError: 'Passwords do not match' });
        else {
            this.setState({ repassError: '' });
        }

        if (this.state.emailError !== '' || this.state.passError !== '' || this.state.nameError !== '' || this.state.repassError !== '') this.setState({ btnDisable: true });
        else this.setState({ btnDisable: false });
    }

    /**
     * This function registers a user
     */
    signup = () => {
        // set log in button to loading state
        this.setState({ btnLoading: true, btnDisable: true });

        let body = {
            name: this.state.name,
            email: this.state.email,
            password: this.state.pass
        }

        axiosService.Post('customers', body)
            .then(res => {
                // Set all state to default
                this.setState({
                    btnLoading: false,
                    btnDisable: false,
                    name: '',
                    email: '',
                    pass: '',
                    repass: ''
                });

                toastService.success('Successful signup you can not sign in with ur credentials ');
            })
            .catch(e => {
                this.setState({
                    btnLoading: false,
                    btnDisable: false
                });

                toastService.error('Error with signup ');
            });
    }


    render() {
        return (
            <React.Fragment>
                <Modal size="mini" open={this.state.open} onClose={this.props.closeModal('signup')} centered={false}>
                    <div>
                        <FiX className="modal-close-icon cl-sec close-m mg-5" onClick={this.props.closeModal('signup')} />
                    </div>
                    <Modal.Content className="modal-action-content">
                        <h3 className="modal-heading">Sign Up</h3>
                        <Form>
                            <Form.Field>
                                <input name="name" value={this.state.name} className="modal-input" type="text" placeholder="Name" onChange={this.handleChange()} onBlur={this.handleChange()} />
                                <small className="error-text">{this.state.nameError}</small>
                            </Form.Field>
                            <Form.Field>
                                <input name="email" value={this.state.email} className="modal-input" type="email" placeholder="Email" onChange={this.handleChange()} onBlur={this.handleChange()} />
                                <small className="error-text">{this.state.emailError}</small>
                            </Form.Field>
                            <Form.Field>
                                <input name="pass" value={this.state.pass} className="modal-input" type="password" placeholder="Password" onChange={this.handleChange()} onBlur={this.handleChange()} />
                                <small className="error-text">{this.state.passError}</small>
                            </Form.Field>
                            <Form.Field>
                                <input name="repass" value={this.state.repass} className="modal-input" type="password" placeholder="Re-type password" onChange={this.handleChange()} onBlur={this.handleChange()} />
                                <small className="error-text">{this.state.repassError}</small>
                            </Form.Field>
                            <center>
                                <Button type="submit" className="login-btn bg-bink cl-pry bg-cl-pry" onClick={this.signup} disabled={this.state.btnDisable} loading={this.state.btnLoading} >{"Sign Up"}</Button>
                            </center>

                            <div className="modal-signup-foot">
                            </div>

                        </Form>
                    </Modal.Content>
                </Modal>
            </React.Fragment>
        );
    }
}

export default RegisterModal;