import {
    SIGNIN_USER,
    SIGNOUT_USER,
    SAVE_CATEGORIES,
    REHYDRATE,
    GENERATE_CART,
    ADD_TO_CART,
    UPDATE_CART_ITEM,
    EMPTY_CART,
    REMOVE_FROM_CART,
    INITIATE_ORDER,
    COMPLETE_INITIALIZATION,
    COMPLETE_SHIPPING,
    UPDATE_ORDER,
    COMPLETE_BILLING,
    COMPLETE_ORDER,
    SAVE_PRODUCTS,
    SAVE_DEPARTMENTS
} from './types';

export const signInUser = (userDetails) => dispatch => {
    dispatch({
        type: SIGNIN_USER,
        payload: userDetails,
    })
}

export const signOutUser = () => dispatch => {
    dispatch({
        type: SIGNOUT_USER
    })
}

export const saveCategories = (categories) => dispatch => {
    dispatch({
        type: SAVE_CATEGORIES,
        payload: categories,
    })
}

export const saveProducts = (products) => dispatch => {
    dispatch({
        type: SAVE_PRODUCTS,
        payload: products,
    })
}

export const saveDepartments = (departments) => dispatch => {
    dispatch({
        type: SAVE_DEPARTMENTS,
        payload: departments,
    })
}


export const rehydrateState = (persistedState) => dispatch => {
    dispatch({
        type: REHYDRATE,
        payload: persistedState
    });
}

export const generateCart = (id) => dispatch => {
    dispatch({
        type: GENERATE_CART,
        payload: id
    })
}
export const addToCart = (items) => dispatch => {
    dispatch({
        type: ADD_TO_CART,
        payload: items
    })
}
export const emptyCart = (cart) => dispatch => {
    dispatch({
        type: EMPTY_CART,
        payload: cart
    })
}
export const removeFromCart = (cart) => dispatch => {
    dispatch({
        type: REMOVE_FROM_CART,
        payload: cart
    })
}
export const updateCartItem = (cart) => dispatch => {
    dispatch({
        type: UPDATE_CART_ITEM,
        payload: cart
    })
}
export const initiateOrder = (cart_id) => dispatch => {
    dispatch({
        type: INITIATE_ORDER,
        payload: cart_id
    })
}
export const completeInitialization = () => dispatch => {
    dispatch({
        type: COMPLETE_INITIALIZATION,
    })
}
export const completeShipping = (shippingId) => dispatch => {
    dispatch({
        type: COMPLETE_SHIPPING,
        payload: shippingId
    })
}
export const updateOrder = (orderId) => dispatch => {
    dispatch({
        type: UPDATE_ORDER,
        payload: orderId
    })
}
export const completeBilling = () => dispatch => {
    dispatch({
        type: COMPLETE_BILLING,
    })
}
export const completeOrder = () => dispatch => {
    dispatch({
        type: COMPLETE_ORDER,
    })
}