import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Input, Image, Button, Divider } from 'semantic-ui-react';
import { FiX } from 'react-icons/fi';
import _ from 'lodash';

// Services
import axiosService from '../../services/axiosService';

class SearchBar extends Component {
    constructor(props) {
        super(props);

        this.state = {
            searchKey: '',
            searchResult: []
        }

        // use lodash debounce to avoid pooling
        this.getDebounceSearch = _.debounce(this.search, 800);
    }

    handleChange = () => (e) => {
        this.setState({ [e.target.name]: e.target.value });
        this.getDebounceSearch();
    }

    search = async () => {
        let search = await axiosService.Get(`products/search?query_string=${this.state.searchKey}&all_words=on`);
        // set the search result 
        if(search.data.rows) {
            this.setState({
                searchResult: search.data.rows
            });
        }
    }

    listSearchResult = () => {
        return this.state.searchResult.map(search => {
            return (
                <React.Fragment key={search.product_id}>
                    <div className="search-item">
                        <Image src={require(`../../img/product_images/${search.thumbnail}`)} />
                        <section>
                            <p className="name">{search.name}</p>
                            <p className="price item-pink">#{search.price}</p>
                            <Button>Buy Now</Button>
                        </section>
                    </div>
                    <Divider/>
                </React.Fragment>
            );
        });
    }

    render() {
        return (
            <div className="search-wrap">
                <span id="close-search" style={{ color: 'white', fontSize: '20px' }}><FiX /></span>
                <div className="search-container">
                    <Input name="searchKey" onChange={this.handleChange()} value={this.state.searchKey} className="search-input" placeholder='Search for products...' />
                    <div className="search-result">
                        {this.listSearchResult()}
                    </div>
                </div>
            </div>
        );
    }
}

const mapStateToProps = state => ({
    user: state.userReducer
});

export default connect(mapStateToProps)(SearchBar);