import React, { Component } from 'react';
import { Button, Grid, Message, Step, Select, Item } from 'semantic-ui-react';
import { completeInitialization, updateOrder, completeShipping, signOutUser, completeOrder, emptyCart } from '../redux/actions';
import { connect } from 'react-redux';
import axiosService from '../services/axiosService';

// import components
import Header from './inc/Header';
import Divider from './inc/Divider';
import Footer from './inc/Footer';
import toastService from '../services/toastService';
import Payment from './inc/Payment';

class Checkout extends Component {

    constructor(props) {
        super(props);
        this.state = {
            shipping: [],
            shipping_id: 0,
            shippingDisabled: true,
            elementFontSize: window.innerWidth < 450 ? '14px' : '18px',
        };

        // special style props for stripe form
        window.addEventListener('resize', () => {
          if (window.innerWidth < 450 && this.state.elementFontSize !== '14px') {
            this.setState({elementFontSize: '14px'});
          } else if (
            window.innerWidth >= 450 &&
            this.state.elementFontSize !== '18px'
          ) {
            this.setState({elementFontSize: '18px'});
          }
        });
    }

    async componentWillMount() {
      const shipping = await axiosService.Get('/shipping/regions')
      this.setState({
        shipping: shipping.data
      })
    }

    // items section for the confirm order steps
    orderItems = () => (
      <React.Fragment>
      <Item.Group>
        {
          this.props.cart.items.map((item) => {
            return (
              <Item key={item.product_id}>
              <Item.Image size='tiny' src={require(`../img/product_images/${item.image}`)} />
        
              <Item.Content>
                <Item.Header>Arrowhead Valley Camp</Item.Header>
                <Item.Meta>
                  <span className='price'>{item.quantity} {item.quantity > 1 ? 'pieces' : 'piece'}</span>
                  <span className='stay'>for ${item.subtotal}</span>
                </Item.Meta>
                <Item.Description>Preference: {item.attributes}</Item.Description>
              </Item.Content>
            </Item>
            )
          })
        }
      </Item.Group>
      <h3>Total: {this.props.cart.items.reduce((acc, curr)=> acc + curr.subtotal, 0)} <span></span></h3>
      <Button type="submit" className="login-btn bg-bink cl-pry bg-cl-pry" onClick={this.handleInitialize}>{"Proceed to shipping"}</Button>
      </React.Fragment>
    )

    // the step section to track order stages
    orderSteps = () => (
      <React.Fragment>
        <Step.Group ordered className="bg-cl-pry cl-pry dp-it-25">
        <Step completed={this.props.order.shippingCompleted} active={this.props.order.mode === 'initialization' ? true : false}>
          <Step.Content>
            <Step.Title>Review Ordered Items</Step.Title>
            <Step.Description>Details of items ordered by You</Step.Description>
          </Step.Content>
        </Step>
        <Step completed={this.props.order.shippingCompleted} active={this.props.order.mode === 'shipping' ? true : false}>
          <Step.Content>
            <Step.Title>Shipping</Step.Title>
            <Step.Description>Choose your shipping options</Step.Description>
          </Step.Content>
        </Step>
    
        <Step completed={this.props.order.billingCompleted} active={this.props.order.mode === 'billing' ? true : false}>
          <Step.Content>
            <Step.Title>Billing</Step.Title>
            <Step.Description>Choose your payment option</Step.Description>
          </Step.Content>
        </Step>
    
        <Step completed={this.props.order.orderCompleted} active={this.props.order.mode === 'confirmOrder' ? true : false}>
          <Step.Content>
            <Step.Title>Confirm Order</Step.Title>
          </Step.Content>
        </Step>
      </Step.Group>
      </React.Fragment>
    )

    // update order initialize state
    handleInitialize = () => {
      this.props.completeInitialization();
    }

    // update order state to shipping step
    handleShipping = async () => {
      const shipping_id = this.state.shipping_id;
      const cart_id = this.props.order.cartId;
      try {
        const order = await axiosService.Post('/orders', {
          shipping_id,
          cart_id,
          tax_id: 1
        }, this.props.user.user.accessToken );
        this.props.updateOrder(order.data.order_id);
        this.props.completeShipping(shipping_id);
        toastService.success('shipping option saved successfully');
      } catch (e) {
        if(e.response.data.status === 403 || e.response.data.status === 401) {
          toastService.error('please signup/login in to proceed to billing')
          this.props.signOutUser();
        }
      }
    }
    
    // update order state to completed
    handleCompleted = () => {
      this.props.completeOrder();
      this.props.emptyCart([]);
      this.props.history.push('/');
    }

    // step section for shipping region
    selectShippingRegion = () => {
      const options = this.state.shipping.map((ship) => {
        return {key: ship.shipping_region_id, value: ship.shipping_region_id, text: ship.shipping_region}
      })
      return (
        <React.Fragment>
          <h3>Select shipping option</h3>
          <Select placeholder='Select your country' name="shipping-selection" options={options} onChange={this.confirmRegion}/>
          <br/>
          <Button type="submit" className="login-btn bg-bink cl-pry bg-cl-pry dp-it-25" disabled={this.state.shippingDisabled} onClick={this.handleShipping}>{"Proceed To billing"}</Button>
        </React.Fragment>
      )
    }

    // section for order completed step
    loadCompleteOrder = () => (
      <React.Fragment>
        <Message>
          <Message.Header>Order placed successfully</Message.Header>
          <p>
          You will receive a confirmation email for your order.
          </p>
        </Message>
        <Button onClick={this.handleCompleted} className="bg-cl-pry cl-pry">Continue shopping</Button>
      </React.Fragment>
    )

    // load stripe payment fprm for the payment section
    loadStripe = () => {
      const {elementFontSize} = this.state;
      return (
        <React.Fragment style={ {maxWidth: '480px'}}>
          <h3>Pay ${this.props.cart.items.reduce((acc, curr)=> acc + curr.subtotal, 0)} Now</h3>
          <Grid>
            <Grid.Column computer={6} tablet={8} mobile={16}>
              <Payment fontSize={elementFontSize}/>
            </Grid.Column>
          </Grid>
        </React.Fragment>
      )
    }

    // for updating state with selected shipping region
    confirmRegion = (e) => {
      const text = e.target.textContent;
      if(text) {
        this.setState({
          shippingDisabled: false,
          shipping_id: this.state.shipping.filter((ship) => ship.shipping_region === text)[0].shipping_region_id
        })
      }
    }

    // render screens based on the current order step
    renderStep = () => {
      if(this.props.order.mode === 'initialization') {
        return this.orderItems();
      } else if (this.props.order.mode === 'shipping') {
        return this.selectShippingRegion();
      } else if (this.props.order.mode === 'billing') {
        return this.loadStripe();
      } else {
        return this.loadCompleteOrder();
      }
    }
    render() {
        return (
            <React.Fragment>

                <div className="app-wrapper">
                    <Header />
                    <div className="app-container">
                        
                        {/* <Grid doubling columns={6}> */}
                        <h1 className="feature-heading">Checkout</h1>
                        <Divider width={'95px'} />
                            {this.orderSteps()}
                            {this.renderStep()}
                        {/* </Grid> */}
                    </div>
                </div>
                <Footer footerStyle='app-footer-grey' />
            </React.Fragment>
        );
    }
}

// import needed state
const mapStateToProps = state => ({
  cart: state.cartReducer,
  order: state.orderReducer,
  user: state.userReducer
});

// export component with redux connection
export default connect(mapStateToProps, { emptyCart, completeInitialization, signOutUser, updateOrder, completeShipping, completeOrder })(Checkout)