import { SIGNIN_USER, SIGNOUT_USER } from '../actions/types';

const initialState = {
    user: {},
    active: false
};

export default (state = initialState, action) => {
    switch (action.type) {
        case SIGNIN_USER:
            return {
                ...state,
                user: action.payload,
                active: true
            }
        case SIGNOUT_USER:
            return {
                ...state,
                user: {},
                active: false
            }
        default:
            return state;
    }
}