// external libraries
import React, { Component } from 'react';
import { injectStripe, CardElement } from 'react-stripe-elements';
import { Button } from 'semantic-ui-react';
import { connect } from 'react-redux';

// services
import axiosService from '../../services/axiosService';
import toastService from '../../services/toastService';

// redux actions
import { completeBilling } from '../../redux/actions';

class _CardForm extends Component {
  createOptions = (fontSize, padding) => {
    return {
      style: {
        base: {
          fontSize,
          color: '#424770',
          letterSpacing: '0.025em',
          fontFamily: 'Source Code Pro, monospace',
          '::placeholder': {
            color: '#aab7c4',
          },
          padding,
        },
        invalid: {
          color: '#9e2146',
        },
      },
    };
  };
  handleSubmit = async (ev) => {
    ev.preventDefault();
    if (this.props.stripe) {
     const token = await this.props.stripe.createToken()
     const description = 'payment for thrill store';
     const order_id = this.props.order.order_id;
     const amount = +this.props.cart.items.reduce((acc, curr)=> acc + curr.subtotal, 0).toFixed(0) * 100;
     const stripeToken = token.token.id;
     if(token) {
       try {
        const payment = await axiosService.Post('/stripe/charge', {
          description, order_id, amount, stripeToken
        });
        if(payment.data) {
          toastService.success('successfully paid for order');
          this.props.completeBilling();
          // }
        }
      } catch (e) {
        toastService.error('payment failed');
      }
      } else {
        toastService.error('make sure you use stripe test card for payment');
      } 
    } else {
      console.log("Stripe.js hasn't loaded yet.");
    }
  };

  render() {
    return (
      <form onSubmit={this.handleSubmit}>
        <label>
          Card details
          <CardElement
            {...this.createOptions(this.props.fontSize)}
          />
        </label>
        <Button className="add-to-cart bg-cl-pry cl-pry dp-it-15">Pay now</Button>
      </form>
    );
  }
}
const CardForm = injectStripe(_CardForm);

// redux state needed in this component
const mapStateToProps = state => ({
  order: state.orderReducer,
  cart: state.cartReducer,
});

// export component connected to redux
export default connect(mapStateToProps, { completeBilling })(CardForm)