import { SAVE_CATEGORIES, SAVE_DEPARTMENTS, SAVE_PRODUCTS, REHYDRATE } from '../actions/types';

const initialState = {
    categories: [],
    departments: [],
    products: [],
};

export default (state = initialState, action) => {
    switch (action.type) {
        case SAVE_CATEGORIES:
            return {
                ...state,
                categories: action.payload
            }
        case SAVE_DEPARTMENTS:
        return {
            ...state,
            departments: action.payload
        }
        case SAVE_PRODUCTS:
        return {
            ...state,
            products: action.payload
        }
        case REHYDRATE:
           return {
               ...state,
               persistedState: action.payload
           }
        default:
            return state;
    }
}