import React, { Component } from 'react';
import { Button, Grid, Image } from 'semantic-ui-react';
import { withRouter } from "react-router";
import { connect } from 'react-redux';

import axiosService from '../services/axiosService';
import toastService from '../services/toastService';

import Header from './inc/Header';
import Footer from './inc/Footer';
import Divider from './inc/Divider';
import Pagination from './inc/Pagination';
import { generateCart, addToCart } from '../redux/actions';

class Category extends Component {

    constructor(props) {
        super(props);

        this.state = {
            category: {},
            categoryProducts: [],
            page: 1,
            perPage: 12,
            count: 0,
            id: 0
        };
    }

    async componentWillMount() {
        // make a request to get a single category with params id
        let id = parseInt(this.props.match.params.id);
        let category = await axiosService.Get(`/categories/${id}`);

        // make a request to get total count first
        let count = await axiosService.Get(`/products/inCategory/${id}`);
        this.setState({
            count: count.data.count
        });

        let categoryProducts = await axiosService.Get(`/products/inCategory/${id}?page=${this.state.page}&limit=${this.state.perPage}&description_length=${this.state.count}`);
        this.setState({
            id,
            category: category.data,
            categoryProducts: categoryProducts.data.rows
        });
    }


    async componentDidUpdate(prevProps, prevState) {
        /**
        * this is the initial render
        * without a previous prop change
        */
       if(prevProps === undefined) {
           return false
       }

       /**
        * new Project in town ?
        */
       if (this.state.id != this.props.match.params.id) {
        let id = parseInt(this.props.match.params.id);

        let category = await axiosService.Get(`/categories/${id}`);

        // make a request to get total count first
        let count = await axiosService.Get(`/products/inCategory/${id}`);
        this.setState({
            count: count.data.count
        });

        let categoryProducts = await axiosService.Get(`/products/inCategory/${id}?page=${this.state.page}&limit=${this.state.perPage}&description_length=${this.state.count}`);
        this.setState({
            id,
            category: category.data,
            categoryProducts: categoryProducts.data.rows
        });
       }

   }
    increasePage = async () => {
        let pageLimit = Math.round((this.state.count / this.state.perPage));

        // make a request to get products for current page 
        // check if the page is greater than the number of available pages
        if (this.state.page > pageLimit) return;
        else {

            this.setState({ page: ++this.state.page });

            let id = parseInt(this.props.match.params.id);

            let categoryProducts = await axiosService.Get(`/products/inCategory/${id}?page=${this.state.page}&limit=${this.state.perPage}&description_length=${this.state.count}`);

            this.setState({
                categoryProducts: categoryProducts.data.rows
            });
        }
    }

    decreasePage = async () => {
        // if page is equals to 1 stop to avoid blank page
        if (this.state.page === 1) return;
        else {
            this.setState({ page: --this.state.page });

            let id = parseInt(this.props.match.params.id);

            // make a request to get products for current page
            let categoryProducts = await axiosService.Get(`/products/inCategory/${id}?page=${this.state.page}&limit=${this.state.perPage}&description_length=${this.state.count}`);

            this.setState({
                categoryProducts: categoryProducts.data.rows
            });
        }
    }

    // methods to navigate to a single product page
    getProduct = (id) => {
        this.props.history.push(`/product/${id}`);
    }

    listCategoryProducts = () => {
        return this.state.categoryProducts.map(product => {
            return (
                <Grid.Column key={product.product_id} >
                    <div className="app-card">
                        <div className="content">
                            <Image src={require(`../img/product_images/${product.thumbnail}`)} />
                            <p className="product-name">
                                {product.name}
                            </p>
                            <p className="product-price">
                                ${product.price}
                            </p>
                        </div>
                        <div className="app-card-overlay point-it" onClick={() => this.getProduct(product.product_id)}>
                            <Button onClick={(e) => this.addToCart(e,product.product_id)}>Buy Now</Button>
                        </div>
                    </div>
                </Grid.Column>
            );
        });

    }

    addToCart = async (e, id) => {
        e.stopPropagation();
        const cart = this.props.cart;
        if(cart.cartId.length === 0) {
            let cartId = await axiosService.Get(`/shoppingcart/generateUniqueId`);
            cartId = cartId.data.cart_id;
            // set the redux user state
            this.props.generateCart(cartId);
            let pushToCart = await axiosService.Post('/shoppingcart/add', {
                product_id: id,
                cart_id: this.props.cart.cartId,
                attributes: 'M, S'
            });
            this.props.addToCart(pushToCart.data)
            toastService.success('successfully added to cart');
        }
        else {
            let pushToCart = await axiosService.Post('/shoppingcart/add', {
                product_id: id,
                cart_id: this.props.cart.cartId,
                attributes: 'M, S'
            });
            this.props.addToCart(pushToCart.data)
            toastService.success('successfully added to cart');
        }
    }
    render() {
        return (
            <React.Fragment>
                <div className="app-wrapper">
                    <Header />

                    <div className="app-container">

                        <div className="category-banner">
                            <div className="category-banner-overlay">
                                <h1 className="feature-heading">
                                    {this.state.category.name}
                                </h1>
                            </div>
                            <Divider width={'95px'} />
                        </div>

                        <Grid doubling columns={6}>
                            {this.listCategoryProducts()}
                        </Grid>

                        <Pagination decreasePage={this.decreasePage} increasePage={this.increasePage} />

                    </div>
                </div>
                <Footer footerStyle='app-footer-grey' />
            </React.Fragment>
        );
    }
}

// import needed states from redux
const mapStateToProps = state => ({
    cart: state.cartReducer
});

// export category component connected to redux and with router privilleges
export default connect(mapStateToProps, { generateCart, addToCart })(withRouter(Category));