import React from 'react';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import { ToastContainer } from 'react-toastify';
import 'semantic-ui-css/semantic.min.css';
import './styles/style.scss';
import 'react-toastify/dist/ReactToastify.css';

// Components
import Home from './components/Home';
import Category from './components/Category';
import Product from './components/Product';
import Department from './components/Department';
import Checkout from './components/Checkout';
const App = () => {
  return (
    <React.Fragment>
      <ToastContainer autoClose={4000} position="top-center" />
      <Router>
        <Switch>
          <Route path="/" component={Home} exact></Route>
          <Route path="/category/:id" component={Category} exact></Route>
          <Route path="/product/:id" component={Product} exact></Route>
          <Route path="/department/:id" component={Department} exact></Route>
          <Route path="/checkout" component={Checkout} exact></Route>
        </Switch>
      </Router>
    </React.Fragment>
  );
}

export default App;
