import React, { Component } from 'react';
import { connect } from 'react-redux';
import { NavLink } from 'react-router-dom';
import { FiShoppingCart, FiUser, FiX, FiSearch, FiMenu } from 'react-icons/fi';

import {
    Menu,
    Sidebar,
    MenuItem,
    Responsive,
    Icon,
} from 'semantic-ui-react';

import { saveCategories, saveProducts, saveDepartments } from '../../redux/actions'
import axiosService from '../../services/axiosService';
import LoginModal from '../modals/SigninModal';
import SignupModal from '../modals/SignupModal';
import CartModal from '../modals/CartModal';
import SearchBar from './Search';

class Header extends Component {

    constructor(props) {
        super(props);

        this.state = {
            signinModal: false,
            signupModal: false,
            cartModal: false,
            visible: false,
            productTotalCount: 0,
            perPage: 12,
            page: 1,
            categories: [],
            departments: [],
            id: 0,
        }

    }


    async componentWillMount() {
        // make a request to get categories and departments
        let categories = this.props.mode ? await axiosService.Get(`/categories/inDepartment/${this.props.id}`) : await axiosService.Get('/categories');
        let departments = await axiosService.Get('/departments');
        

        let id = this.props.mode ? this.props.id : 0;
        this.props.saveCategories(categories.data);
        this.props.saveDepartments(departments.data);
        this.setState({
            id
        });

    }
    async componentDidUpdate(prevProps, prevState) {
        /**
        * this is the initial render
        * without a previous prop change
        */
       if(prevProps === undefined) {
           return false
       }

       if (this.props.mode && this.state.id != this.props.id) {
        let id = parseInt(this.props.id);

        // make a request to get all categories or categories under a department
        let categories = this.props.mode ? await axiosService.Get(`/categories/inDepartment/${this.props.id}`) : await axiosService.Get('/categories');
        // update state with the new categories
        this.props.saveCategories(categories.data);
       }

   }
    showSideMenu = () => this.setState({ visible: true });

    hideSideMenu = () => this.setState({ visible: false });

    handleChange = (e, { value }) => {
        this.setState({ qstvalue: value })
        if (value === 'email') this.setState({ forgotPhoneState: 'none', qstbtndis: false, forgotEmailState: 'block' });
        else if (value === 'phone') this.setState({ forgotEmailState: 'none', qstbtndis: false, forgotPhoneState: 'block' });
    }

    handleUserDataChange(type = '', e) {
        if (type === 'change') {
            this.setState({ [e.target.name]: e.target.value });
            this.formErrors('change');
        }

        this.setState({ [e.target.name]: e.target.value });
    }
    // open a specific modal
    openModal = (modal) => () => {
        if (modal === 'signin') {
            this.setState({
                signupModal: false,
                cartModal: false,
                signinModal: true
            });
        } else if (modal === 'cart') {
            this.setState({
                loginModal: false,
                signupModal: false,
                cartModal: true
            });
        } else if (modal === 'signup') {
            this.setState({
                loginModal: false,
                cartModal: false,
                signupModal: true
            });
        }
    }

    closeModal = (modal) => () => {
        if (modal === 'signin') this.setState({ signinModal: false });
        else if (modal === 'cart') this.setState({ cartModal: false });
        else if (modal === 'signup') this.setState({ signupModal: false });
    };

    listCategories = () => {
        return this.props.util.categories.slice(0, 8).map(cat => {
            return (
                <li className="nav-list dp-it-15" key={cat.category_id}>
                    <NavLink to={`/category/${cat.category_id}`}>
                        {cat.name}
                    </NavLink>
                </li>
            );
        });
    }
    listDepartments = () => {
        return this.props.util.departments.map(dept => {
            return (
                <li className="nav-list" key={dept.department_id}>
                    <NavLink to={`/department/${dept.department_id}`}>
                        {dept.name}
                    </NavLink>
                </li>
            );
        });
    }
    listSideCategories = () => {
        return this.state.categories.map(cat => {
            return (
                <Menu.Item as='a' href={`/category/${cat.category_id}`} key={cat.category_id}>
                    {cat.name}
                </Menu.Item>
            );
        });
    }
    displayCheckout = () => {
        if(this.props.order.mode != 'pending') {
            return (
                <React.Fragment>
                    <NavLink to="/checkout">
                        <span className="cl-pry dp-it-15">Checkout</span>
                        <Menu.Item>
                        <Icon name="arrow alternate circle right"/>
                        </Menu.Item>
                    </NavLink>
                </React.Fragment>
            )
        }
    }

    render() {
        return (
            <React.Fragment>
            {/** Modals */}
            <LoginModal modalState={this.state.signinModal} closeModal={this.closeModal} />
            <SignupModal modalState={this.state.signupModal} closeModal={this.closeModal} />
            <CartModal modalState={this.state.cartModal} closeModal={this.closeModal} />
            {/** End of Modals */}
                {/** Header */}
                <nav className="nav" id="nav">
                    <Menu.Item className="mr40">
                        <NavLink to="/">
                            <h2 className="nav-logo">THRILL-STORE</h2>
                        </NavLink>
                    </Menu.Item>

                    {this.listDepartments()}

                    <span className="right">
                        <Menu.Item >
                            <FiSearch className="top-icon" id="open-search" />
                        </Menu.Item>
                        <Menu.Item>
                            <FiUser className="top-icon" />
                        </Menu.Item>
                        <Responsive as={MenuItem} {...Responsive.onlyMobile}>
                            <FiMenu onClick={this.showSideMenu} className="top-icon side-menu-trigger" />
                        </Responsive>
                    </span>
                </nav>
                <Menu
                    borderless
                    className="nav2"
                    id="nav2"
                >
                    <Menu.Item>
                        <p>Hi! {(() => {
                            if (this.props.userDetails.active) {
                                return <span>{this.props.userDetails.user.customer.schema.name}</span>
                            } else {
                                return (
                                    <React.Fragment>
                                        <span className="item-pink" onClick={this.openModal('signin')}>Sign in</span> or <span className="item-pink" onClick={this.openModal('signup')}>Register</span>
                                    </React.Fragment>
                                );
                            }
                        })()} </p>
                    </Menu.Item>
                        {this.listCategories()}
                    <Menu.Menu position='right'>
                        <Menu.Item>
                            <FiShoppingCart className="top-icon" onClick={this.openModal('cart')} /><span className="cart-qty">{this.props.cart.items.length}</span>&nbsp; <span>Your Bag: &nbsp;${+this.props.cart.items.reduce((acc, curr)=> acc + curr.subtotal, 0).toFixed(2)}</span>
                        </Menu.Item>
                        { this.displayCheckout() }
                        
                    </Menu.Menu>
                </Menu>

                <Sidebar
                    as={Menu}
                    animation='overlay'
                    direction='right'
                    vertical
                    visible={this.state.visible}
                    width='wide'
                >
                    <div className="sidebar-close-wrap">
                        <FiX onClick={this.hideSideMenu} className="sidebar-close-icon" />
                    </div>
                    {this.listSideCategories()}
                </Sidebar>
                <SearchBar />
            </React.Fragment >
        )
    }
}

const mapStateToProps = state => ({
    userDetails: state.userReducer,
    cart: state.cartReducer,
    order: state.orderReducer,
    util: state.utilReducer,
});

export default connect(mapStateToProps, { saveCategories, saveProducts, saveDepartments })(Header);
