import { GENERATE_CART, ADD_TO_CART, EMPTY_CART, REMOVE_FROM_CART, UPDATE_CART_ITEM  } from '../actions/types';

const initialState = {
    cartId: '',
    items: [],
};

export default (state = initialState, action) => {
    switch (action.type) {
        case GENERATE_CART:
            return {
                ...state,
                cartId: action.payload,
                items: []
            }
        case ADD_TO_CART:
        return {
            ...state,
            items: action.payload
        }
        case REMOVE_FROM_CART:
        return {
            ...state,
            items: action.payload
        }
        case UPDATE_CART_ITEM:
        return {
            ...state,
            items: action.payload
        }
        case EMPTY_CART:
        return {
            ...state,
            items: action.payload
        }
        default:
            return state;
    }
}