import React, { Component } from 'react';
import { Button, Modal, Table, Card, Image, Icon } from 'semantic-ui-react';
import { FiX, FiMinus, FiPlus } from 'react-icons/fi';
import { emptyCart, removeFromCart, updateCartItem, initiateOrder } from '../../redux/actions';
import { connect } from 'react-redux';
import SweetAlert from 'react-bootstrap-sweetalert';
import axiosService from '../../services/axiosService';
import toastService from '../../services/toastService';
import { withRouter } from 'react-router-dom';
import _ from 'lodash';

class CartModal extends Component {
    constructor(props) {
        super(props);

        this.state = {
            open: false,
            showAlert: false,
            confirmText: 'Are you sure you sure you want to proceed',
            confirmBtnBsStyle: 'yes continue',
            alertMsg: 'this action is irreversible',
            alertMode: 'remover',
            removeId: 0
        };
        // use lodash debounce to avoid pooling
        this.getDebounceCartUpdate = _.debounce( async function(id) {
            const value = document.querySelector(`[name=qty-${id}]`).value;

            // retrieve count items from server
            const updatedCart  = await axiosService.Put(`/shoppingcart/update/${id}`, {
                quantity: value
            });
            // update cart state with items
            this.props.updateCartItem(updatedCart.data);
        }, 2000);
    }

    componentWillMount() {
        this.setState({
            open: this.props.modalState
        });
    }

    componentWillReceiveProps(nextProps) {
        this.setState({
            open: nextProps.modalState
        });
    }
    // increment quantity to add to cart
    increment = (e, id) => {
        const value = document.querySelector(`[name=qty-${id}]`).value;
        document.querySelector(`[name=qty-${id}]`).value = parseInt(value) + 1;
        this.getDebounceCartUpdate(id);
    }

    // decrement quantity to add to cart
    decrement = (e, id) => {
        const value = document.querySelector(`[name=qty-${id}]`).value;
        if(value > 1) {
            document.querySelector(`[name=qty-${id}]`).value = parseInt(value) - 1;
        }
        this.getDebounceCartUpdate(id);
    }

        /**
     * This function updates input states
     */
    handleChange = () => (e) => {
            this.setState({ targetQuantity: parseInt(e.target.value ) || 0});
    }
    listCartItems = () => {
        return this.props.cart.items.map(item => {
            return (
                <Table.Row key={item.item_id}>
                <Table.Cell>
                    <div className="cart-item-wrap">
                        <Card style={{ width: '100px', float: 'left' }} className="mr-5">
                            <Card.Content style={{ width: '100px', height: '100px' }}>
                                <Image fluid src={require(`../../img/product_images/${item.image}`)} className="item-thumbnail"/>
                            </Card.Content>
                        </Card>
                        <div className="cart-name-wrap pl-15">
                            <h4 className="lh">{item.name}</h4>
                            {/* <h5 className="desc">Men </h5> */}
                        </div>
                    </div>
                </Table.Cell>
                <Table.Cell><p className="item-size">{item.attributes.split(',')[0]}</p></Table.Cell>
                <Table.Cell><p className="item-size">{item.attributes.split(',')[1]}</p></Table.Cell>
                <Table.Cell>
                    <div className="qty-input-wrap">
                        <button><FiMinus onClick={(e) => this.decrement(e,item.item_id)} /></button><input value={item.quantity} name={`qty-${item.item_id}`} className="product-qty-m" onChange={(id) => this.getDebounceCartUpdate(item.item_id)}></input><button><FiPlus onClick={(e) => this.increment(e,item.item_id)} /></button>
                    </div>
                </Table.Cell>
                <Table.Cell><p className="cart-price">${item.subtotal}</p></Table.Cell>
                <Table.Cell> 
                        <Icon name='cancel' onClick={(e) => this.deleteConfirm(e,item.item_id)} className="error-text point-it"/>
                        {/* <Icon name='add' className="success-text point-it"></Icon> */}
                        {/* <Icon name='edit' className="info-text point-it" onClick={(e) => this.editConfirm(e,item.item_id)}></Icon> */}
                </Table.Cell>
            </Table.Row>
            );
        });
    }

    closeModal = () => {
        this.setState({
            open: false,
            showAlert: false
        })
    }

    completeDelete = async (id) => {
        // delete item from server
        let deleted = await axiosService.Delete(`/shoppingcart/removeProduct/${id}`);
        if(deleted.data) {
            const updatedCart = this.props.cart.items.filter((cart) => cart.item_id !== id )
            // update cart state
            this.props.removeFromCart(updatedCart)
            toastService.success('successfully removed from cart');
        }
        toastService.error('an error occured while trying to remove item');
    }

    completeEmpty = async (id) => {
        // emptyCart on server
        let deleted = await axiosService.Delete(`/shoppingcart/empty/${id}`);
        if(deleted.data) {
            // update cart state
            this.props.emptyCart([])
            toastService.success('successfully emptied cart');
        } else {
            toastService.error('an error occured while trying to remove item');
        }
    }

    // show a confirmation dialogue for deleting
    deleteConfirm = async (e, id) => {
        e.stopPropagation();
        this.setState({
            showAlert: true,
            confirmText: 'yes, remove',
            confirmBtnBsStyle: 'danger',
            alertMsg: 'Item will be removed from cart',
            alertMode: 'remove',
            removeId: id
        })
    }

    // display a confirmation dialogue for emptying cart
    emptyConfirm = async (e, id) => {
        e.stopPropagation();
        this.setState({
            showAlert: true,
            confirmText: 'yes, clear cart',
            confirmBtnBsStyle: 'danger',
            alertMsg: 'Cart will be cleared',
            alertMode: 'empty'
        })
    }
    
    // handle a confirmation dialogue trigger
    handleAlert = () => {
        switch(this.state.alertMode) {
            case "remove":
                this.completeDelete(this.state.removeId);
                break;
            case "empty":
                this.completeEmpty(this.props.cart.cartId);
                break;
            default:
                console.log('not found')
          } 
        this.setState({
            showAlert: false
        })
    }

    // navigate to checkout
    handleCheckout = () => {
        this.props.initiateOrder(this.props.cart.cartId);
        this.props.history.push(`/checkout`);
    }

    render() {
        return (
            <React.Fragment>
                <SweetAlert 
                    warning
                    showCancel
                    confirmBtnText={this.state.confirmText}
                    confirmBtnBsStyle={this.state.alertStyle}
                    cancelBtnBsStyle="default"
                    title="Are you sure?"
                    show={this.state.showAlert}
                    onConfirm={this.handleAlert}
                    onCancel={this.hideAlert}
                >
                    {this.state.alertMsg}
                </SweetAlert>
                <Modal size='large' open={this.state.open} onClose={this.props.closeModal('cart')} centered={false}>
                    <div>
                        <FiX className="modal-close-icon cl-sec close-l mg-5 point-it"  onClick={this.props.closeModal('cart')} />
                    </div>
                    <Modal.Content className="modal-action-content">
                        <h4 className="cart-details">{this.props.cart.items.length} Items In Your Cart</h4>
                        <Table id="borderless">
                            <Table.Header >
                                <Table.Row>
                                    <Table.HeaderCell className="table-item bg-cl-pry cl-pry">Item</Table.HeaderCell>
                                    <Table.HeaderCell className="bg-cl-pry cl-pry">Size</Table.HeaderCell>
                                    <Table.HeaderCell className="bg-cl-pry cl-pry">Color</Table.HeaderCell>
                                    <Table.HeaderCell className="bg-cl-pry cl-pry">Quantity</Table.HeaderCell>
                                    <Table.HeaderCell className="bg-cl-pry cl-pry">Price</Table.HeaderCell>
                                    <Table.HeaderCell className="bg-cl-pry cl-pry">Actions</Table.HeaderCell>
                                </Table.Row>
                            </Table.Header>

                            <Table.Body>
                                {this.listCartItems()}
                            </Table.Body>
                        </Table>
                    </Modal.Content>
                    <div className="cart-modal-footer">
                        <div className="button-wrap fl-right pd-bt-25">
                            <Button className="back cl-pry bg-cl-pry" onClick={this.props.closeModal('cart')}>Back to Shop</Button>
                            <Button className="check cl-pry bg-cl-pry" onClick={this.handleCheckout}>Checkout</Button>
                            <Button className="check cl-pry bg-cl-pry" onClick={this.emptyConfirm}>Empty Cart</Button>
                        </div>
                    </div>
                </Modal>
            </React.Fragment>
        );
    }
}

// import needed states from redux
const mapStateToProps = state => ({
    cart: state.cartReducer,
    order: state.orderReducer
});

// export Component connected to reduxstore with router privilleges
export default connect(mapStateToProps, { emptyCart, removeFromCart, updateCartItem, initiateOrder })(withRouter(CartModal));