import React, { Component } from 'react';
import { Button, Grid, Image, Form, Card } from 'semantic-ui-react';
import { FiMinus, FiPlus } from 'react-icons/fi';
import { NavLink } from 'react-router-dom';
import { connect } from 'react-redux';

import axiosService from '../services/axiosService';

import Header from './inc/Header';
import Footer from './inc/Footer';
import Divider from './inc/Divider';
import toastService from '../services/toastService';
import { generateCart, addToCart } from '../redux/actions';

class Product extends Component {

    constructor(props) {
        super(props);

        this.state = {
            product: {},
            otherProducts: [],
            quantity: 0,
            attributes: [],
            sortedAttributes: {},
            id: 0,
            cart: {}
        };
    }

    sortAttributes(attributes) {
        let result = [];
        let keys = [];
        attributes.forEach((attr) => {
            if(!keys.includes(attr.attribute_name)) {
                keys.push(attr.attribute_name);
                result.push({
                    name: attr.attribute_name,
                    values: [attr.attribute_value]
                });
                let sortedAttributes = this.state.sortedAttributes;
                sortedAttributes[attr.attribute_name] = attr.attribute_value;
                this.setState(sortedAttributes);
            } else {
                result[keys.indexOf(attr.attribute_name)].values.push(attr.attribute_value);
            }
        });
        return result;
    }

    async componentWillMount() {
        // make a request to get a single product with params id
        let id = parseInt(this.props.match.params.id);
        let product = await axiosService.Get(`/products/${id}`);
        let otherProducts = await axiosService.Get('/products?page=1&limit=4');
        let attributes = await axiosService.Get(`/attributes/inProduct/${id}`);
        attributes = this.sortAttributes(attributes.data);

        this.setState({
            product: product.data,
            otherProducts: otherProducts.data,
            id,
            attributes,
        });
    }

    async componentDidUpdate(prevProps, prevState) {
        /**
        * this is the initial render
        * without a previous prop change
        */
       if(prevProps === undefined) {
           return false
       }

       /**
        * new Project in town ?
        */
       if (this.state.id != this.props.match.params.id) {
        let id = parseInt(this.props.match.params.id);

        let attributes = await axiosService.Get(`/attributes/inProduct/${id}`);
        attributes = this.sortAttributes(attributes.data);

        // make a request to get total count first
        let product = await axiosService.Get(`/products/${id}`);
        this.setState({
            product: product.data,
            id,
            attributes
        });
       }

    }

    // increment quantity to add to cart
    increment = () => {
        this.setState({
            quantity: this.state.quantity + 1
        }); 
    }

    // decrement quantity to add to cart
    decrement = () => {
        this.setState({
            quantity: this.state.quantity > 0 ? this.state.quantity - 1 : 0
        });
    }

        /**
     * This function updates input states
     */
    handleChange = () => (e) => {
        if(e.target.name === 'quantity'){
            this.setState({ [e.target.name]: parseInt(e.target.value ) || 0});
        }
        else {
            let sortedAttributes = this.state.sortedAttributes;
            sortedAttributes[e.target.name] = e.target.value;
            this.setState({sortedAttributes});
        }
        // this.formErrors();
    }

    listProducts = () => {
        return this.state.otherProducts.map(product => {
            return (
                <Grid.Column computer={4} mobile={8} key={product.product_id}>
                    <div className="app-card">
                    <div className="content">
                        <NavLink to={`/product/${product.product_id}`}>
                            <Image src={require(`../img/product_images/${product.thumbnail}`)} />
                        </NavLink>
                        <p className="product-name">
                            {product.name}
                        </p>
                        <p className="product-price">
                            ${product.price}
                        </p>
                    </div>
                    </div>
                </Grid.Column>
            );
        });
    }
    listAttributes = () => {
        return this.state.attributes.map(attr => {
            return (
                <Grid.Column computer={8} mobile={16} key={attr.name}>
                    <Form>
                        <Form.Field>
                            <label>Select {attr.name}</label>
                            <select name={attr.name} onChange={this.handleChange()}>
                            {attr.values.map((val) => {
                                return <option value={val} key={val}>{val}</option>
                            })}
                            </select>
                        </Form.Field>
                    </Form>
                </Grid.Column>
            );
        });
    }
     
    // todo refactor this to a component
    addToCart = async () => {
        const cart = this.props.cart;
        if(cart.cartId.length === 0) {
            let cartId = await axiosService.Get(`/shoppingcart/generateUniqueId`);
            cartId = cartId.data.cart_id;
            this.props.generateCart(cartId);
            let pushToCart = await axiosService.Post('/shoppingcart/add', {
                product_id: this.state.product.product_id,
                cart_id: this.props.cart.cartId,
                attributes: Object.values(this.state.sortedAttributes).join(', ')
            });
            this.props.addToCart(pushToCart.data)
            toastService.success('successfully added to cart');
        }
        else {
            let pushToCart = await axiosService.Post('/shoppingcart/add', {
                product_id: this.state.product.product_id,
                cart_id: this.props.cart.cartId,
                attributes: Object.values(this.state.sortedAttributes).join(', ')
            });
            this.props.addToCart(pushToCart.data)
            toastService.success('successfully added to cart');
        }
    }

    render() {
        return (
            <React.Fragment>
                <div className="app-wrapper">
                    <Header />
                    <div className="app-container">
                        <Card id="single-Product-card" className="card-pad dp-it-25">
                            <Card.Content id="content">
                                <Grid>
                                    <Grid.Column computer={7} tablet={8} mobile={16}>
                                        {
                                          (() => {
                                            if(this.state.product.image !== undefined) {
                                                return <Image  src={require(`../img/product_images/${this.state.product.image}`)}/>
                                            }
                                          })()  
                                        
                                        }
                                    </Grid.Column>
                                    <Grid.Column computer={9} tablet={8} mobile={16}>
                                        <h3>{this.state.product.name}</h3>
                                        <h4>
                                            #{this.state.product.price}                                        
                                        </h4>
                                    </Grid.Column>
                                    <div className="qty-input-wrap">
                                            <button><FiMinus onClick={this.decrement}/></button><input name="quantity" type="text" value={this.state.quantity} onChange={this.handleChange()} className="product-qty"></input><button><FiPlus onClick={this.increment}/></button> 
                                        </div>
                                        {this.listAttributes()}
                                        <Button className="add-to-cart dp-it-15" onClick={this.addToCart}>Add to Cart</Button>
                                        <h5 className="dp-it-15">Product description</h5>
                                        <p>
                                            {this.state.product.description}
                                        </p>
                                </Grid>
                            </Card.Content>
                        </Card>
                        <h1 className="feature-heading">You may also like</h1>
                        <Divider width={'95px'} />
                        <Grid>
                            {this.listProducts()}
                        </Grid>
                    </div>
                </div>
                <Footer footerStyle='app-footer-white' />
            </React.Fragment>
        );
    }
}
const mapStateToProps = state => ({
    cart: state.cartReducer
});

export default connect(mapStateToProps, { generateCart, addToCart })(Product);