import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Button, Modal, Form } from 'semantic-ui-react';
import { FiX } from 'react-icons/fi';
import Validator from 'validator';

// Services
import axiosService from '../../services/axiosService';
import toastService from '../../services/toastService';
import { signInUser } from '../../redux/actions';

class LoginModal extends Component {
    constructor(props) {
        super(props);

        this.state = {
            open: false,
            email: '',
            emailError: '',
            pass: '',
            passError: '',
            btnDisable: false,
            btnLoading: false
        };
    }

    componentWillMount() {
        this.setState({
            open: this.props.modalState
        });
    }

    componentWillReceiveProps(nextProps) {
        this.setState({
            open: nextProps.modalState
        });
    }

    /**
     * This function closes the modal by setting open state to false
     */
    closeModal = () => {
        this.setState({
            open: false
        })
    }

    /**
     * This function updates input states
     */
    handleChange = () => (e) => {
        this.setState({ [e.target.name]: e.target.value });
        this.formErrors();
    }

    /**
    * form validation this will disable the signin button if the checks are not passed
    */
    formErrors = () => {
        if (!Validator.isEmail(this.state.email) || this.state.email === '') this.setState({ emailError: 'Please enter a valid email' });
        else this.setState({ emailError: '' });

        if (this.state.pass === '') this.setState({ passError: 'Please enter a valid passowrd' });
        else this.setState({ passError: '' });

        if (this.state.emailError !== '' || this.state.passError !== '') this.setState({ btnDisable: true });
        else this.setState({ btnDisable: false });
    }

    /**
     * This function logs in a user with the email and pass state
     */
    login = () => {
        // set log in button to loading state
        this.setState({ btnLoading: true, btnDisable: true });

        let body = {
            email: this.state.email,
            password: this.state.pass
        }

        axiosService.Post('customers/login', body)
            .then(res => {
                // Set all state to default
                this.setState({
                    btnLoading: false,
                    btnDisable: false,
                    email: '',
                    pass: ''
                });

                toastService.success('Successful signin ');

                delete res.data.expires_in;

                // // set the redux user state
                this.props.signInUser(res.data);

                // close the modal after 2 seconds
                setTimeout(() => {
                    this.closeModal();
                }, 2000)
            })
            .catch(e => {
    
                this.setState({
                    btnLoading: false,
                    btnDisable: false
                });

                toastService.error('Error with signin ');
            });
    }

    render() {
        return (
            <React.Fragment>
                <Modal size="mini" open={this.state.open} onClose={this.props.closeModal('signin')} centered={false} className="dp-it-50">
                    <div>
                        <FiX className="modal-close-icon cl-sec close-m mg-5" onClick={this.props.closeModal('signin')} />
                    </div>
                    <Modal.Content className="modal-action-content">
                        <h3 className="modal-heading">Sign In</h3>
                        <Form>
                            <Form.Field>
                                <input name="email" value={this.state.email} className="modal-input" placeholder="Email" type="email" onChange={this.handleChange()} onBlur={this.handleChange()} />
                                <small className="error-text">{this.state.emailError}</small>
                            </Form.Field>
                            <Form.Field>
                                <input name="pass" value={this.state.pass} className="modal-input" type="password" placeholder="Password" onChange={this.handleChange()} onBlur={this.handleChange()} />
                                <small className="error-text">{this.state.passError}</small>
                            </Form.Field>

                            <center>
                                <Button type="submit" className="login-btn bg-bink cl-pry bg-cl-pry" onClick={this.login} disabled={this.state.btnDisable} loading={this.state.btnLoading}>{"Sign In"}</Button>
                            </center>

                        </Form>
                    </Modal.Content>
                </Modal>
            </React.Fragment>
        );
    }
}

// import needed states
const mapStateToProps = state => ({
    user: state.userReducer
});

// export modal connected to redux store
export default connect(mapStateToProps, { signInUser })(LoginModal);