import { INITIATE_ORDER, COMPLETE_INITIALIZATION, COMPLETE_SHIPPING, COMPLETE_BILLING, COMPLETE_ORDER, UPDATE_ORDER  } from '../actions/types';

const initialState = {
  mode: 'pending',
  initializationCompleted: false,
  shippingCompleted: false,
  billingCompleted: false,
  orderCompleted: false,
  shippingId: 0,
  cartId: '',
  order_id: 0,
};

export default (state = initialState, action) => {
    switch (action.type) {
        case INITIATE_ORDER:
            return {
                ...state,
                cartId: action.payload,
                mode: 'initialization',
                initializationCompleted: false,
                shippingCompleted: false,
                billingCompleted: false,
                orderCompleted: false,
                shippingId: 0,
                order_id: 0,
            }
        case COMPLETE_INITIALIZATION:
        return {
            ...state,
            shippingCompleted: true,
            mode: 'shipping',
        }
        case COMPLETE_SHIPPING:
        return {
            ...state,
            shippingCompleted: true,
            mode: 'billing',
            shippingId: action.payload
        }
        case UPDATE_ORDER:
        return {
            ...state,
            order_id: action.payload
        }
        case COMPLETE_BILLING:
        return {
            ...state,
            billingCompleted: true,
            mode: 'order'
        }
        case COMPLETE_ORDER:
        return {
            ...state,
            orderCompleted: true,
            mode: 'pending',
            initializationCompleted: false,
            shippingCompleted: false,
            billingCompleted: false,
            orderCompleted: false,
            shippingId: 0,
            order_id: 0,
            cartid: '',
        }
        default:
            return state;
    }
}