import React, { Component } from 'react';
import { Button, Grid, Image } from 'semantic-ui-react';
import { NavLink } from 'react-router-dom';
import { connect } from 'react-redux';

import axiosService from '../services/axiosService';
import toastService from '../services/toastService';

import Header from './inc/Header';
import Divider from './inc/Divider';
import Footer from './inc/Footer';
import Pagination from './inc/Pagination';
import { generateCart, addToCart } from '../redux/actions';

class Home extends Component {

    constructor(props) {
        super(props);

        this.state = {
            products: [],
            count: 0,
            perPage: 12,
            page: 1
        };
    }

    async componentWillMount() {
        // make a request to get total count first
        let count = await axiosService.Get('/products');
        this.setState({
            count: count.data.count
        });

        // make a request to get products with pergination
        let products = await axiosService.Get(`/products?page=${this.state.page}&limit=${this.state.perPage}&description_length=${this.state.count}`);

        this.setState({
            products: products.data
        });

    }

    /**
    * This function takes a user to the next page
    */
    increasePage = async () => {
        let pageLimit = Math.round((this.state.count / this.state.perPage));

        // make a request to get products for current page 
        // check if the page is greater than the number of available pages
        if (this.state.page > pageLimit) return;
        else {

            this.setState({ page: ++this.state.page });

            let products = await axiosService.Get(`/products?page=${this.state.page}&limit=${this.state.perPage}&description_length=${this.state.count}`);

            this.setState({
                products: products.data
            });
        }
    }

    /**
     * This function takes a user to the previous page
     */
    decreasePage = async () => {
        // if page is equals to 1 stop to avoid blank page
        if (this.state.page === 1) return;
        else {
            this.setState({ page: --this.state.page });
            // make a request to get products for current page
            let products = await axiosService.Get(`/products?page=${this.state.page}&limit=${this.state.perPage}`);

            this.setState({
                products: products.data
            });
        }
    }

    getProduct = (id) => {
        this.props.history.push(`/product/${id}`);
    }

    addToCart = async (e, id) => {
        e.stopPropagation();
        const cart = this.props.cart;
        if(cart.cartId.length === 0) {
            let cartId = await axiosService.Get(`/shoppingcart/generateUniqueId`);
            cartId = cartId.data.cart_id;

            // set the redux user state
            this.props.generateCart(cartId);
            let pushToCart = await axiosService.Post('/shoppingcart/add', {
                product_id: id,
                cart_id: this.props.cart.cartId,
                attributes: 'M, S'
            });
            this.props.addToCart(pushToCart.data)
            toastService.success('successfully added to cart');
        }
        else {
            let pushToCart = await axiosService.Post('/shoppingcart/add', {
                product_id: id,
                cart_id: this.props.cart.cartId,
                attributes: 'M, S'
            });
            this.props.addToCart(pushToCart.data)
            toastService.success('successfully added to cart');
        }
    }
    listProducts = () => {
        return this.state.products.map(product => {
            return (
                <Grid.Column key={product.product_id} >
                    <div className="app-card">
                        <div className="content">
                            <NavLink to={`/product/${product.product_id}`}>
                                <Image src={require(`../img/product_images/${product.thumbnail}`)} />
                            </NavLink>
                            <p className="product-name">
                                {product.name}
                            </p>
                            <p className="product-price">
                                ${product.price}
                            </p>
                        </div>
                        <div className="app-card-overlay point-it" onClick={() =>this.getProduct(product.product_id)}>
                            <Button onClick={(e) => this.addToCart(e,product.product_id)}>Buy Now</Button>
                        </div>
                    </div>
                </Grid.Column>
            );
        });
    }

    render() {
        return (
            <React.Fragment>

                <div className="app-wrapper">
                    <Header />
                    <div className="home-banner">
                        <div className="home-banner-overlay">
                            <h1>
                                We are destined to thrill you
                            </h1>
                            <p>Premium products tailored for your needs and shipped to your doorsteps at affordable rates.</p>
                            <Button id="home-overlay-btn" className="home-overlay-btn">Shop</Button>
                        </div>
                    </div>
                    <div className="app-container">
                        <h1 className="feature-heading">Feature Products</h1>
                        <Divider width={'95px'} />
                        
                        <Grid doubling columns={6}>
                            {this.listProducts()}
                        </Grid>

                        <Pagination decreasePage={this.decreasePage} increasePage={this.increasePage} />
                    </div>
                </div>
                <Footer footerStyle='app-footer-grey' />
            </React.Fragment>
        );
    }
}

const mapStateToProps = state => ({
    cart: state.cartReducer
});

export default connect(mapStateToProps, { generateCart, addToCart })(Home);
