import { toast } from 'react-toastify';

export default {
    success: (msg) => {
        return toast(msg, {
            type: 'success',
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true
        });
    },

    error: (msg) => {
        return toast(msg, {
            type: 'error',
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true
        }); 
    }
}